.Buy {
    display: flex;
    flex-direction: row;
    
    overflow: hidden;
}

.leftSidebar {
    display: flex;
    flex-direction: column;
    width: 300px;
    background-color: #FF9A02;
}

.search {
    padding: 12px;
    /* width: 80%; */
    background-color: #FFFFFF;
    margin: 20px;
    margin-top: 20px;
    text-align: center;
    border-radius: 20px;
    color: #4F4F4F;
}

.searchItem {
    display: flex;
    flex-direction: column;
    margin: 20px;
    justify-content: center;
    background-color: #FFFFFF;
    border-radius: 20px;
}

.searchItemTall {
    display: flex;
    flex-direction: row;
    margin: 20px;
    /* justify-content: center; */
    background-color: #FFFFFF;
    border-radius: 20px;
    /* float: left; */
}

.searchImg {
    margin: 20px;
    border-radius: 20px;
    margin-bottom: 0px;
}

.searchImgTall {
    margin: 20px;
    margin-right: 0px;
    border-radius: 20px;
}

.totPrice {
    display: flex;
    flex-direction: row;
    margin-left: 15px;
}

.totPriceTall {
    display: flex;
    flex-direction: row;
    margin-left: 15px;
    margin-top: 15px;
}

.price {
    background-color: #02FF9A;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    display: flex;
    align-items: center; 
    justify-content: center;
    margin: 10px;
    margin-left: 5px;
    margin-right: 5px;
    /* margin-left: 20px; */
    /* margin-right: 0px; */
}

.rightSidebar {
    display: flex;
    flex-direction: column;
    width: 300px;
    background-color: #FF9A02;
    /* margin-left: auto; */
    margin-right: 0;
}

.filter {
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    margin: 20px;
    margin-top: 5px;
}

.filterItem {
    background-color: #02FF9A;
    border-radius: 50px;
    padding: 10px;
    font-size: 20px;
    margin: 10px;
    margin-top: 25px;
    text-align: center;
    margin-left: 5px;
    margin-right: 30%;
}